.main_location_detail {
  padding-top: 108px;
  @media screen and (max-width: $pads-media) {
    padding-top: 50px;
  }
  // Banner
  .banner {
    background: url('../Images/72.png') no-repeat;
    background-position: center;
    background-size: cover;
    height: 304px;
    margin-bottom: 16px;
    @media screen and (max-width: $pads-media) {
      margin-bottom: 8px;
    }
    .slogan {
      width: 100%;
      @media screen and (max-width: $pads-media) {
        padding: 0 48px
      }
      @media screen and (max-width: $phones-media) {
        padding: 0 11px
      }
      &__title {
        font-size: 64px;
        line-height: 80px;
        margin-right: 10px;
        @media screen and (max-width: $pads-media) {
          font-size: 48px;
          line-height: 80px;
        }
        @media screen and (max-width: $phones-media) {
          font-size: 28px;
        }
      }
      .icon {
        @media screen and (max-width: $pads-media) {
          width: 48px;
          height: 48px;
        }
        @media screen and (max-width: $phones-media) {
          width: 24px;
          height: 24px;
        }
      }
    }
  }
  // Content
  .content {
    margin-bottom: 156px;
    .selectBox {
      margin-bottom: 32px;
      &__title {
        font-size: 24px;
        line-height: 30px;
        margin-right: 22px;
        @media screen and (max-width: $pads-media) {
          font-size: 16px;
          margin-right: 23px;
        }
        @media screen and (max-width: $phones-media) {
          font-size: 14px;
          margin-right: 16px;
          width: 50%;
        }
      }
      .selectWrapper {
        border-radius: 0;
        display:inline-block;
        overflow:hidden;
        background: #FFFFFF;
        border: 1px solid #CED4DA;
        .select {
          padding: 10px 19px;
          width: 266px;
          height: 45px;
          border:0px;
          background: #FFFFFF;
          outline:none;
          @media screen and (max-width: $pads-media) {
            width: 237px;
            height: 46px;
          }
          @media screen and (max-width: $phones-media) {
            width: 100vw;
            height: 36px;
          }
        }
      }
    }
    .detailRow {
      .store__card {
        flex-basis: 30%;
        @media screen and (max-width: $pads-media) {
          flex-basis: 100%;
          margin-bottom: 32px;
          display: flex;
        }
        .storeImg {
          @media screen and (max-width: $pads-media) {
            flex-basis: 100%;
          }
        }
        .store__body {
          transition: all .5s linear;
          background-color: #FFFFFF;
          @media screen and (max-width: $pads-media) {
            flex-basis: 100%;
            padding: 24px;
            padding-right: 0;
          }
          .store__title {
            padding: 16px 24px;
            border: 1px solid #00000020;
            font-size: 24px;
            line-height: 33px;
            @media screen and (max-width: $pads-media) {
              font-size: 16px;
              line-height: 24px;
            }
          }
          .list__ul {
            border: 1px solid #00000020;
            padding: 16px 24px;
          }
          .list__li {
            margin-bottom: 8px;
            &:last-child {
              margin-bottom: 0;
            }
            .icon {
              width: 26px;
              height: 26px;
              margin-right: 8px;
              @media screen and (max-width: $pads-media) {
                width: 22px;
                height: 16px;
              }
            }
            .label {
              font-size: 20px;
              line-height: 28px;
              @media screen and (max-width: $pads-media) {
                font-size: 14px;
                margin-right: 21px;
              }
            }
          }
        }
        .store__footer {
          background-color: #000000;
          width: 100%;
          height: 68px;
          color: #FFFFFF;
          font-size: 20px;
          line-height: 30px;
          @media screen and (max-width: $pads-media) {
            display: none;
          }
        }
      }
      .store__map {
        flex-basis: 70%;
        padding-left: 24px;
        @media screen and (max-width: $pads-media) {
          flex-basis: 100%;
          padding-left: 0px;
        }
      }
    }
  }
}